.section-course-hero {
  .content, .img {
    margin-top: var(--content-offset);
  }

  .content {
    @include flex-all-sb;
    align-items: flex-start;
    flex-wrap: wrap;
    gap: 24px;
  }

  .third-heading {
    margin-top: 16px;
  }

  .text-content {
    max-width: 1000px;
  }

  .btn {
    margin-top: 48px;
  }
}




@include respond(mobile) {
  .section-course-hero {
   .img {
    display: none;
   }
  }
}