.section-hero {
  .container {
    padding: 64px;
    background-color: var(--clr-dark);
    border-radius: 16px 800px 16px 16px;
    flex-wrap: wrap-reverse;
  }
}

.main-imgs {
  position: relative;

  .main-img {
    max-width: 40vw;
    @include hover {
      cursor: pointer;
      transform: scale(1.05);
    }
  }

  .main-icon {
    position: absolute;
    @include hover {
      cursor: pointer;
      transform: scale(1.1);
    }

    &-1 {
      left: 5%;
      top: -20%;
    }
    &-2 {
      right: -6%;
      top: -28%;
    }
    &-3 {
      right: -13%;
      bottom: 52%;
    }
  }
}

.cta {
  max-width: 563px;

  & > * + * {
    margin-top: 48px;
  }
}


@include respond(desktop-lg) {
  .main-imgs {
    .main-img {
      max-width: 33vw;
    }
    .main-icon {
      &-3 {
        display: none;
      }
    }
  }
}

@include respond(desktop) {
  .main-imgs {
    .main-icon {
      &-1, &-2 {
        display: none;
      }
    }
  }
}

@include respond(tablet) {
  .section-hero {
    .container {
      padding: 48px;
      border-radius: 16px;
    }

    .main-imgs {
      display: none;
    }
  }
}

@include respond(mobile) {
  .section-hero {
    .container {
      padding: 32px;
    }
  }
}