.section-course-info {
  .cards {
    display: flex;
    flex-wrap: wrap;
    gap: 48px;
  }

  .card {      
    @include flex-all-center;
    @include hover {
      transform: scale(1.02);
    }

    flex: 1 1 calc(25% - 48px);
    padding: 100px;
    margin: 0;
    background-color: var(--clr-accent-10);
    border-radius: var(--border-radius-md);
    cursor: pointer;

    &:nth-child(n + 5) {
      flex-basis: calc(33% - 48px); 
    }
 

  }
  
  .third-heading {
    color: var(--clr-accent);
  }
}

@include respond(mobile) {
  .section-course-info {
    .card {      
      padding: 64px;
    }
  }
}