.section-program {
  .top {
    @include flex-all-sb;
    flex-wrap: wrap;
    gap: 32px;
  }

  .modules {
    margin-top: var(--content-offset);

    & > * + * {
      margin-top: 48px;
    }
  }

  .module {
    cursor: pointer;

    position: relative;
    @include flex-all-sb;
    column-gap: 24px;
    
    padding: 64px 80px;
    border: var(--border);
    border-radius: var(--border-radius-md);

    .descr {
      max-height: 0;
      will-change: max-height;
      opacity: 0;
      overflow: hidden;
      box-sizing: content-box;
      transition: all 0.3s ease-out;
      

      &__list {
        margin-top: 24px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 12px 64px;
      }  
    }


  }

  
  .module.hidden {
    display: none;
  }

  .module.active  {
    .descr {
      opacity: 1;
      max-height: 100%;
      margin-top: 48px;
    }

    .plus {
      transform: rotate(45deg);
      transition: all 0.3s ease-out;
    }
  }

  .text {
    margin-top: 8px;
  }  

  .plus {
    position: absolute;
    top: 85px;
    right: 40px;
    transition: all 0.3s ease-out;

    svg {
      width: 48px;
      height: 48px;
    }
  }
}


@include respond(mobile) {
  .section-program {

    .modules {  
      & > * + * {
        margin-top: 32px;
      }
    }
  
    .module {
      padding: 32px 48px;
    }

    .plus {
      display: none;
    }
    .descr {
      .descr__list {
        grid-template-columns: 1fr;
        row-gap: 4px;
      }  
    }
  }
}

@include respond(mobile-sm) {
  .section-program p.text {
    display: none;
  }

  .section-program {

    .modules {  
      & > * + * {
        margin-top: 24px;
      }
    }
  
    .module {
      padding: 24px 32px;
    }


    .third-heading {
      font-size: 24px;
    }
  }
}