.section-success {
  .flex-container {
    align-items: flex-start;
  }
  .content {
    max-width: 760px;

    .list {
      display: inline-block;
      li:before {
        content: url('../../../img/check.png');
        padding-right: 12px;
      }
    }
  }

  .success-img {
    max-width: 540px;
    max-height: 540px;
    width: 50vw;
    border-radius: var(--border-radius-md);
  }

  .text {
    margin-top: 48px;
  }
}

@include respond(desktop) {
  .section-success {
    .content {
      max-width: 100%;
    }

  }
}


@include respond(medium) {
  .section-success {
    .text {
      margin-top: 32px;
    }
  }
}

@include respond(mobile) {
  .section-success {
    .content {
      max-width: 100%;
      text-align: left;
    }
    .flex-container {
      justify-content: flex-start;
    }
  }
}