.section-about-hero {
  .first-heading {
    max-width: 1440px;
  }

  .main-img {
    margin-top: var(--content-offset);
    max-width: 1680px;
    max-height: 800px;
    border-radius: var(--border-radius-md);
  }

  .text {
    margin-top: var(--content-offset);
    max-width: 850px;
  }
}

@include respond(mobile) {
  .section-about-hero {
    .main-img {
      display: none;
    }
  }
}