.section-salary {
  .content {
    margin-top: var(--content-offset);
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(650px, 1fr));
    gap: 48px;
  }
}

.salary {

  &__fourth-heading {
    color: var(--clr-accent);
  }

	&__info {
    border-radius: var(--border-radius-sm);
    background-color: var(--clr-accent-10);
    padding: 64px;

    .text {
      margin-top: 32px;
    }

    .text-content {
      margin-top: 32px;
      @include flex-all-sb;
    }
	}

	&__slider {
    padding: 64px;
    max-width: 100%;

    .input-range {
      margin-top: 48px;
      width: 100%;
      appearance: none;
      height: 16px;
      background-color: var(--clr-accent-10);
      border-radius: var(--border-radius-md);

      &::-webkit-slider-thumb  {
        appearance: none;
        width: 16px;
        height: 16px;
        background-color: var(--clr-accent-10);
        border-radius: var(--border-radius-md);
        outline: 8px solid var(--clr-accent);
        cursor: pointer;
      }
      
    }

    .text {
      margin-top: 8px;
    }
	}
  
}


.labels {
  margin-top: 16px;
  @include flex-all-sb;
}

@include respond(medium) {
  .section-salary {
    .content {
      grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
      gap: 48px;
    }
  }
  
  .salary {
    &__info {
      padding: 40px;
    }
  
    &__slider {
      padding: 40px;
    }
  }
}

@include respond(mobile) {
  .section-salary {
    .content {
      grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
      gap: 32px;
    }

    .text-content {
      flex-direction: column;
      align-items: flex-start;
      row-gap: 8px;
    }

    .sixth-heading {
      font-size: 14px;
    }
  }
  
  .salary {
    &__info {
      padding: 32px;
    }
  
    &__slider {
      padding: 32px;

      .input-range {
        margin-top: 32px;
        height: 12px;
  
        &::-webkit-slider-thumb  {
        
          width: 12px;
          height: 12px;
          outline: 6px solid var(--clr-accent);
        }
        
      }
    }
  }
}