.section-procces {
  .third-heading {
    background-color: var(--clr-accent);
    padding: 64px;
  }

  .proceses {
    margin-top: var(--content-offset);
    & > * + * {
      margin-top: 32px;
    }
  }

  .procces {
    @include flex-all-sb;

    @include hover {
      transform: scale(1.01);
      cursor: pointer;
    }
    column-gap: 64px;
    background-color: var(--clr-dark-2);
    border-radius: var(--border-radius-md);
    overflow: hidden;
  }
}

@include respond(tablet) {
  .section-procces {
    .third-heading {
      padding: 48px;
    }
  
    .proceses {
      margin-top: var(--content-offset);
      & > * + * {
        margin-top: 32px;
      }
    }
  
    .procces {
      column-gap: 32px;

      .third-heading {}

    }
  }
}

@include respond(mobile) {
  .section-procces {
    .procces {
      padding: 32px;
    }
  
    .proceses {
      margin-top: var(--content-offset);
      & > * + * {
        margin-top: 24px;
      }
    }
  
    .procces {
      column-gap: 24px;

      .third-heading {
        display: none;
      }

    }
  }
}

@include respond(mobile-sm) {
  .section-procces {
    .procces {
      .fifth-heading {
        font-size: 20px;
      }

    }
  }
}