.section-info {
  .container {
    @include flex-all-sb;
    gap: 32px;
  }

  .item {
    @include flex-all-center;
    column-gap: 32px;
  
    .first-heading {
      color: var(--clr-accent);
    }
    .fifth-heading {
      max-width: 260px;
    }
  }
}

@keyframes count-animation {
  0% {
    opacity: 0;
    transform: translateY(10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.count-animation {
  animation: count-animation 0.5s ease-in-out;
}

@include respond(tablet) {
  .section-info {
    .container {
      @include flex-all-sb;
      justify-content: center;
      flex-wrap: wrap;
      gap: 48px;
    }
   .item {
    .fifth-heading {
      max-width: 100%;
    }
   }
  }
}