.results {
  margin-top: var(--content-offset);
  display: grid;
  grid-template-columns: repeat(2, minmax(320px, 1fr));
  gap: 64px 128px;

  .img {
    @include hover {
      transform: scale(1.04);
    }
    cursor: pointer;
  }

}

.result {
  @include flex-v-center;
  column-gap: 64px;

  .fourth-heading {
    max-width: 430px;
  }
}

@include respond(tablet) {
  .results {
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));

    .fourth-heading {
      max-width: 100%;
    }
  }

  .result {
    flex-wrap: wrap;
    gap: 32px;

    .img {
      max-width: 120px;
    }
  }
}

@include respond(medium) {
  .result {
    .img {
      max-width: 80px;
    }
  }
}
@include respond(mobile) {
  .results {
    row-gap: 48px;
  }
  .result {
    .fourth-heading {
      font-size: 20px;
    }
  }
}