.section-contact-us {
  .content {
    @include flex-all-sb;
    flex-wrap: wrap;
    gap: 24px;
    max-width: 1440px;
    border: var(--border);
    border-radius: var(--border-radius-md);
    padding: 48px 64px;
    margin: 0 auto;
  }
}

@include respond(mobile) {
 .section-contact-us {
  .content {
    padding: 32px 48px;
  }
 }
}

